<template>
  <div>
    <app-header></app-header>

    <b-container>
      <h4 class="my-2">Доступные тесты</h4>
      <b-list-group>
        <div v-for="test in getPublishedTests"
             :key="test._id">
          <b-list-group-item class="d-flex justify-content-between align-items-center"
                             :id="test._id">
            <a href="#">{{ test.title }}</a>
            <b-badge variant="primary" pill>{{ test.questions.length }}</b-badge>
          </b-list-group-item>
          <b-tooltip :target="test._id">
            <span v-html="test.description" style="font-size: .8em"></span>
          </b-tooltip>
        </div>
      </b-list-group>
    </b-container>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
    }
  },

  computed: {
    ...mapGetters({
      'getPublishedTests': 'test/publishedTests'
    })
  },

  mounted () {
    this.$store.dispatch('test/GET_TESTS')
  },

  methods: {
  }
}
</script>



